/* Extra large devices (desktops, less than 1280px) */
@media (max-width: 1722px){
	.hashtag{
		font-size: 16px;
	}

	.nav{
		margin-left: 56px;
	}

	.nav .nav__menu li{
		margin-right: 60px;
	}

	.ads__text{
		width: 201px;
	}

	.ads_download{
		width: 105px;
		padding: 0;
		margin-right: 15px;
	}

	.reminder{
		height: 68px;
	}

	.reminder__play{
		width: 68px;
		height: 68px;
	}

	.reminder__play .angle{
		border-width: 21px;
		border-top-width: 13px;
    	border-bottom-width: 13px;
	}

	.reminder__text{
		padding-left: 39px;
	}

	.news{
		padding: 19px 0 12px 29px;
	}

	.news__text{
		width: 220px;
	}

	.news__link{
		font-size: 12px;
	}
}

/* Medium devices (tablets, less than 992px) */
@media (max-width: 992px){
	.hashtag{
		display: none;
	}
	
	.nav .nav__menu li{
		margin-right: 40px;
	}
	.offer{
		top: 35%;
    	transform: translateY(-35%);
	}

	.offer__subtitle{
		margin-top: 18px;
	}

	.bottom_panel{
		flex-direction: column;
	}

	.bottom_panel__right{
		order: -1;
	}

	.ads{
		position: absolute;
		top: -89px;
		right: 5%;
	}

	.reminder{
		margin-top: 0;
	}

	.bottom_panel > div{
		width: 100%;
	}

	.news{
		height: unset;
	}
}

/* Extra small devices (portrait phones, less than 576px) */
@media(max-width: 576px){
	.offer{
		margin: 0;
		position: static;
    	margin-top: 52px;
    	transform: unset;
	}

	.offer__title{
		width: 255px;
	}

	.offer__subtitle{
		width: 160px;
		hyphens: manual;
		-webkit-hyphens: manual;
	    -moz-hyphens: manual;
	    -ms-hyphens: manual;
	}

	.offer__btn{
		margin-top: 21px;
	}

	.ads{
		position: static;
		margin: 36px 3% 26px 3%;
	}

	.bottom_panel__right{
		width: 94% !important;
		margin: 0 auto;
		position: relative;
	}

	.news:first-child{
		position: absolute;
		top: -41px;
		width: 55%;
	}

	.news:last-child{
		margin-left: auto;
		position: relative;
		z-index: 2;
		width: 51%;
	}

	.news{
		padding: 15px 31px 18px 20px;
	}

	h3.news__title{
		font-size: 12px;
	}

	.news__text {
	    margin: 10px 0 0 0;
	    width: 134px;
	}

	.news__link{
		display: none;
	}

	.ads__text{
		width: 134px;
		margin-right: 26px;
	}

	.ads_download{
		width: 70px;
		margin-right: 18px;
	}

	/* Modal */
	.remodal{
		width: 90%;
	}

	.remodal .wrap{
		padding: 20px 15px;
	}

	.modal__title{
		font-size: 16px;
	}

	.modal__text{
		font-size: 12px;
		margin-bottom: 0;
	}

	.btn-group .btn{
		font-size: 9px;
		padding: 0;
		height: 40px;
		justify-content: center;
	}
}


/* For device height */
@media (max-height: 475px){
	body{
		font-size: 9px;
	}
	.ads, .nav, .lang{
		display: none;
	}
	.menu_bars{
		display: block;
	}
	.offer{
		margin-left: -0px;
	}
	.offer__title{
		font-size: 30px;
		line-height: 37px;
	}

	.offer__subtitle{
		font-size: 16px;
		line-height: 20px;
	}

	.offer__btn{
		display: none;
	}

	.bottom_panel{
		flex-direction: row;
		align-items: flex-end;
	}

	.bottom_panel > div{
		width: 50%;
	}

	.bottom_panel__left{
		order: -1;
	}

	.news{
		padding: 15px 31px 18px 20px;
	}

	h3.news__title{
		font-size: 12px;
	}

	.news__text {
	    margin: 10px 0 0 0;
	    width: 134px;
	}

	.news__link{
		display: none;
	}
}

@media (max-width: 600px) and (max-height: 320px){
	#header{
		min-height: 450px;
	}

	.offer__title, .offer__subtitle{
		width: auto;
	}
	
	.bottom_panel{
		flex-direction: column;
	}

	.bottom_panel__right{
		order: -2;
	}

	.bottom_panel > div{
		width: 100% !important;
	}

	.news:first-child{
		position: static;
	}
}

@media (max-width: 320px) and (max-height: 663px){
	#header{
		height: auto;
		min-height: 663px;
	}
}