/* Modal */
.remodal-overlay{
  background: rgba(39, 39, 41, 0.6);
}

.remodal-wrapper{
  padding: 0;
}

.remodal{
  width: 420px;
  padding: 0px;
  margin-bottom: 0;
  padding-bottom: 0;
  background: #F5F5F5;
  box-shadow: 0px 4px 44px rgba(0, 0, 0, 0.25);
}

.remodal .wrap{
  padding: 40px;
}

.modal__title{
  font-size: 20px;
  text-transform: none;
  color: $sec_text_color;
  letter-spacing: unset;
}

.modal__text{
  margin: 23px 0 31px 0;
  font-size: 18px;
}

.btn-group{
  display: flex;
}

.btn-group button{
  width: 50%;
  height: 65px;
  padding: 21px 45px;
  border-bottom: 0;
  color: #6A2C9A;
  background: transparent;
}

.btn-group button:first-child,
.btn-group button:last-child{
  border-top: 1px solid #B4B4B4;
  border-right: 1px solid #B4B4B4;
}

button a {
  color: $main_color;
}

.btn-group button:last-child{
  border-top: 1px solid #B4B4B4;
  border-right: none;
}

.btn-group button:hover{
  background: #6A2C9A;
  color: #fff;
}