// Modules
@import "modules/variables";

// Partials
@import "partials/reset";
@import "partials/buttons";
@import "partials/typography";
@import "partials/menu";

// Vendor
@import "vendor/slideout";
@import "vendor/remodal";

// Hero
#header {
  height: 100vh;
  position: relative;
  padding-top: 40px;
  color: $main_text_color;
  background: url("../img/background.jpg") no-repeat center top / cover;
}

.hashtag{
  position: absolute;
  top: 50%;
  left: 0;
  
  transform-origin: left;
  transform: rotate(-90deg) translate(-50%, 30px);

  letter-spacing: 0.36em;
  color: rgba(255, 255, 255, 0.5);
  font-size: 20px;
}

.offer{
  margin: 0 0 0 61px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.offer__btn{
  margin-top: 84px;
}

.bottom_panel{
  position: absolute;
  bottom: 0;
  width: 100%;
}

.bottom_panel__left,
.bottom_panel__right{
  width: 50%;
}

.bottom_panel__left{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.bottom_panel__left .ads{
  justify-content: center;
  align-items: center;
}

.ads__text{
  width: 255px;
}

.reminder{
  height: 102px;
  margin-top: 37px;
  background: $main_bg;
}

.reminder__play{
  width: 102px;
  height: 102px;
  background: $main_color;
  display: flex;
}

.reminder__play .angle{
  width: 0;
  height: 0;
  border: 31px solid transparent;
  border-left-color: #fff;
  border-right: none;
  border-top-width: 23px;
  border-bottom-width: 23px;
  margin: auto;
  cursor: pointer;
}

.reminder__text{
  display: flex;
  padding-left: 59px;
  flex-direction: column;
  justify-content: center;
}

.reminder__text_title{
  letter-spacing: 0.05em;
  line-height: 30px;
  text-transform: uppercase;
}

.reminder__text__subtitle{
  font-weight: 500;
  text-transform: uppercase;
  line-height: 17px;
}

.news{
  height: 100%;
  padding: 27px 22px 0 29px;
  color: $sec_text_color;
  width: 50%;
}

.news__title{
  line-height: 24px;
  font-size: 20px;
  letter-spacing: 0;
  text-transform: none;
}

.title__next_show{
	color: $sec_text_color;
}

.news__text{
  margin: 21px 0 29px 0;
  width: 319px;
}

.news__link{
  text-transform: uppercase;
  font-size: 16px;
  color: $main_color;
  letter-spacing: 0.03em;
}

.news:first-child{
  background: #fff;
}

.news:last-child{
  background: $main_color;
  color: $main_text_color;
}

@import "partials/responsive/general-responsive";
@import "partials/responsive/main-responsive";